.card1 p.c1,.card2 p.c2,.card3 p.c3,.card4 p.c4,.card5 p.c5,.card6 p.c6,.card7 p.c7 {
    display: none;
    visibility: hidden;
    transition: 0.04s;
    color: black;
    text-align: left; 
    justify-content: center;
    padding-top: 30px;
    padding-left: 40px;
    padding-right: 40px;
    font-size: 16px;
    padding-bottom: 30px;
}

.card1:hover p.c1,.card2:hover p.c2,.card3:hover p.c3,.card4:hover p.c4,.card5:hover p.c5,.card6:hover p.c6,.card7:hover p.c7 {
    display: block;
    visibility: visible;
    transition: 0.04s;
    color: black;
    text-align: left; 
    justify-content: center;
    padding-top: 30px;
    padding-left: 40px;
    padding-right: 40px;
    font-size: 16px;
    padding-bottom: 30px;
}

.card1,.card2,.card3,.card4,.card5,.card6,.card7 {
    border-radius: 15px;
    height: 400px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 30%;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.4);
}

.card1 p.t1,.card2 p.t1,.card3 p.t1,.card4 p.t1,.card5 p.t1,.card6 p.t1,.card7 p.t1 {
    color: black;
    font-size: 26px;
