            text-decoration: underline red;
    text-underline-offset: 5px;
    font-family: 'Poppins', sans-serif;
    padding-top: 10px;
    font-weight: bold;
}

.card1:hover,.card1:active,.card2:hover,.card2:active,.card3:hover,.card3:active,.card4:hover,.card4:active,.card5:hover,.card5:active,.card6:hover,.card6:active,.card7:hover,.card7:active {
